.gpt3__footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
    /* color:#fff; */
    font-family: var(--font-family);
}

.gpt3__footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom:3rem;
}

.gpt3__footer-heading h1{
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
}

.gpt3__footer-btn{
    display:flex;
    justify-content: center;
    align-items: center;
    padding:1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom:10rem;
    cursor: pointer;
    transition: all 0.6s ease;
    color:#fff;
}

.gpt3__footer-btn:hover{
    background:#fff;
    color:#000;
}

.gpt3__footer-btn p {
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
    box-sizing: border-box;
    word-spacing: 2px;
}

.gpt3__footer-btn > p > svg{
    vertical-align: middle;
}

.gpt3__footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align:left;
}

.gpt3__footer-links div{
    width: 250px;
    margin:1rem;
}


.gpt3__footer-links_logo{
    display: flex;
    flex-direction: column;
    /* padding-bottom:2rem; */
}

.gpt3__footer-links_logo img{
    width:100px;
    /* height: 30px; */
    margin-bottom:1rem;
}

.gpt3__footer-links_logo p{
    font-size: 12px;
    font-weight: 400;
    color:#fff;
}

.gpt3__footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3__footer-links_div h4{
    font-size: 14px;
    font-weight: 700;
    color:#fff;
    line-height: 16px;
    margin-bottom:1rem;
}

.gpt3__footer-links_div p{
    font-size: 12px;
    font-weight: 400;   
    color:#fff; 
    line-height: 14px;
    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt3__footer-copyright{
    margin-top:3rem;
    text-align: center;
    width:100%;
}

.gpt3__footer-copyright p{
    font-size: 12px;
    font-weight: 400;
    color:#fff;
        
}

/*mobile*/
@media screen and (max-width: 850px) {
    .gpt3__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
    .gpt3__footer-btn{
        margin-bottom:6.5rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3__footer-links div {
        margin: 1rem 0;
    }

    .gpt3__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
    .gpt3__footer-btn{
        margin-bottom:5rem;
    }
}

@media screen and (max-width: 400px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
    .gpt3__footer-btn{
        margin-bottom:4rem;
    }
}