.gpt3__possibility{
    display: flex;
    flex-direction: row;
    /* justify-content: center;
    align-items: center; */
}

.gpt3__possibility-image{
   flex:1;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   margin: 2rem;
}

.gpt3__possibility-image img{
    width:100%;
    height:100%;
}

.gpt__possibility-content {
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    margin:2rem;
}

.gpt__possibility-content h4{
    color: #71E5FF;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom:0.5rem; 
} 

.gpt__possibility-content h4:last-child{
    color: var(--color-subtext);
}

.gpt__possibility-content h1{
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px; 
    margin:1rem 0;
}

.gpt__possibility-content p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    margin-bottom:1rem; 
}

.gpt3__possibility.section__padding{
    padding: 1rem 6rem
}

@media screen and (max-width: 950px){
    .gpt3__possibility{
        flex-direction: column;
    }
    .gpt3__possibility.section__padding{
        padding: 0rem 4rem
    }
}


@media screen and (max-width: 550px){
    .gpt3__possibility.section__padding{
        padding: 0rem 1.5rem
    }
    .gpt3__possibility-image{
        margin:0 2rem;
    }
}
