.gpt3__cta{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin:4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.gpt__cta-content p{
    color: #0E0E0E;
    font-family: var(--font-family);
    font-size: 12px;  
    font-weight: 500;
    line-height: 30px; 
}

.gpt__cta-content h3{
    color: #000;
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-btn button{
    border-radius: 40px;
    background: #000;
    color:#fff;   
    font-family: var(--font-family);
    padding:1rem 2.5rem;     
    font-size: 18px;
    font-weight: 700;
    line-height: 30px; 
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 210px;
    transition:all 0.6s ease;
    flex:0.9;
    width:100%;
}

.gpt3__cta-btn button:hover{
    background: #fff;
    color:#000;
}


@media screen and (max-width:900px){
    .gpt__cta-content h3{
        line-height: 35px;
    }
}

@media screen and (max-width:650px){
    .gpt3__cta{
        flex-direction: column;
    }
    .gpt3__cta-btn{
        margin:2rem;
    }

}

@media screen and (max-width:550px){
    .gpt3__cta{
        margin:4rem 2rem;
    }
    .gpt__cta-content h3{
        font-size:18px;
        line-height:30px;
    }

    .gpt__cta-btn button{
        font-size:14px;
        line-height:25px;
    }
    .gpt3__cta-btn{
        margin:1rem;
    }
}