.gpt3__header{
    display: flex;
}

.gpt3__header-content{
    display: flex;
    flex:1;
    justify-content:center ;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 700;
    font-size:60px;
    line-height: 75px;
    letter-spacing: -0.05em;
}

.gpt3__header-content p{
    font-family: var(--font-family);
    font-size:20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top:1.5rem;
}

.gpt3__header-content__input{
    width:100%;
    margin:2rem 0 1rem;
    display: flex;
}


.gpt3__header-content input{
    flex:2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size:20px;
    line-height: 27px;
    background: var(--color-footer);
    
    border: 2px solid var(--color-footer);
    padding:0 1rem;
    outline:none;
    color:#fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.gpt3__header-content__input button{
    flex:0.85;
    width:100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    color:#fff;
    font-weight: 400;
    background: #FF4820;
    border: none;
    outline: none;
    cursor: pointer;
    /* border-radius: 0 5px 5px 0; */
    padding: 0 1rem;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}



.gpt3__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top:2rem;
}

.gpt3__header-content__people img{
    width:180px;
    height:37px;
}

.gpt3__header-content__people p{
    margin:1rem;
    font-size: 12px;
    font-weight: 400;
    font-family: var(--font-family);
    color:#fff;
    text-align:center;
    line-height: 38px;
    /* width: 581.921px;
    height: 37.611px; */
}

.gpt3__header-image {
    flex:1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img{
    width:100%;
    height:100%;
    
}


@media screen and (max-width:1050px){
    .gpt3__header{
        flex-direction:column ;
    }
    .gpt3__header-content{
        margin: 0 0 3rem;
    }
}

@media screen and (max-width:650px){
    .gpt3__header h1{
        font-size:48px ;
        line-height: 60px;
    }
    .gpt3__header p{
        font-size:16px ;
        line-height: 24px;
    }
    .gpt3__header-content__people{
        flex-direction: column;
    }

    .gpt3__header-content__people p {
        margin:0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width:490px){
    .gpt3__header h1{
        font-size:36px ;
        line-height: 48px;
    }
    .gpt3__header p{
        font-size:14px ;
        line-height: 24px;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 12px;
        line-height: 16px;
    }
}