.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-heading{
    flex:1;
    display: flex;
    justify-content:first flex-start ;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right:5rem;
}

.gpt3__features-heading h1{
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px; 
}

.gpt3__features-heading p{
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px; 
    margin-top:2rem;
}

.gpt3__features-container{
    flex:1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}



@media screen and (max-width:900px){
    .gpt3__features{
        flex-direction: column;
    }

    .gpt3__features-container{
        margin:2rem 0rem;
    }

}

@media screen and (max-width:550px){
    .gpt3__features-heading h1{
        font-size:28px;
        line-height: 38px;
    }

    .gpt3__features-container{
        margin:2rem 0rem;
        
    }
}